@import "./component/common/common_component.scss";
@import "./component/test_report.scss";

.report-layout {
  background-color: white;
  height: 100%;
  background-image: url("./component/icons/png/report-background.png");
  background-size: contain;
}

.navigate-bar {
  padding: 5px 5px 5px 5px;
  margin-bottom: 10px;
}

.report-card-border {
  border-radius: 4px;
  box-shadow: 0 4px 40px 0 rgba(1, 77, 109, 0.1);
}

.report-table {
  .ant-table{
    overflow-x: auto;
    .ant-table-thead {
      th {
        color: white !important;
        background-color: #003153 !important;
      }
    }
  }
}

.react-resizable {
  position: relative;
  background-clip: padding-box;
}

.react-resizable-handle {
  position: absolute;
  right: -5px;
  bottom: 0;
  z-index: 1;
  width: 10px;
  height: 100%;
  cursor: col-resize;
}

/* width */
::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: transparent;
}

/* Handle */
::-webkit-scrollbar-thumb {
  border: 3px solid transparent;
  background: #555;
}

.report-content {
  overflow: hidden;
}

.result-tree {
  padding-top: 10px;
  padding-right: 10px;
  padding-left: 10px;
  border-right: 0.5px solid #eee;
  display: flex;
  flex-direction: column;

  .ant-tree.ant-tree-directory {
    background: inherit;
    .ant-tree-treenode-selected::before {
      background: #91d5ff !important;
    }
    .ant-tree-treenode:hover,
    .ant-tree-switcher:hover {
      cursor: pointer;
    }
    .ant-tree-treenode:hover::before {
      background-color: #91d5ff;
    }
    .ant-tree-treenode-selected {
      color: rgba($color: #000000, $alpha: 0.85) !important;
      .anticon {
        svg {
          g {
            filter: none;
          }
        }
      }
    }
    .ant-tree-node-content-wrapper {
      display: flex;
      flex-direction: row;
    }
  }
}

.full-size-div {
  width: 100%;
  height: 100%;
}

.report-content-area {
  overflow: scroll;
  padding: 30px 30px 30px 30px;
}

.report-content-divider-collapse {
  margin-top: 0 !important;
  margin-bottom: 0 !important;
}

.report-content-collapse-header {
  .ant-collapse-header {
    padding-top: 0 !important;
  }

}

.report-input-desc{
  margin: 0;
  .ant-descriptions-view{
    table{
      width:100%;
      table-layout: fixed !important;
      .ant-descriptions-row{
        width: 100%;
        .ant-descriptions-item-label{
          width:200px;
        }
        .ant-descriptions-item-content{
          .ant-typography{
            margin-bottom: 0;
          }
        }

      }
    }
  }
}

