.test-report-info {
  display: flex;
  flex-direction: column;
}

.test-report-info-card {
  flex: 1 1 auto;
}

.report-info-card {
  margin-top: 20px;
  width: 100%;
}

.report-chart-card {
  .ant-card-body {
    align-items: center;
    display: flex;
    justify-content: center;
    height: 100%;
  }
}

.result-tree-collapse{
  .ant-tree-treenode{
    .ant-tree-switcher{
      width:0;
    }
    .ant-tree-node-content-wrapper {
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }
}
