.result-info-card {
  .ant-card-body {
    padding: 0;
    .ant-row {
      height: 100%;
    }
  }
  .result-info-icon {
    align-items: center;
    display: flex;
    justify-content: center;
    padding: 10px;
  }
  .result-info-value {
    display: flex;
    flex-direction: column;
    padding: 15px 45px 15px 45px;
  }
  width: 100%;
}
